
import { AlpineDataComponent } from '@lacasadejuana/types';
import TomSelect from 'tom-select';
import { IFilterDefinition, waitFor } from '../../components';
import { getFiltrosDisponiblesOptions } from '../decorators/getFiltrosDisponiblesOptions';

export interface IAvailableFiltersControl {
    filter_id: number;
    tomSelect: TomSelect;
    createAvailableFiltersControl($el: HTMLElement): void;
}

export const availableFiltersControl = (filter_id: number) => ({

    id_filter: filter_id,
    tomSelect: null as TomSelect,
    destroy() {
        if (this.tomSelect) {
            this.tomSelect.destroy()
        }
        this.tomSelect = null;

    },
    /*get filter_id() {
        return this.id_filter
    },
    set filter_id(id) {
        if (this.tomSelect) {
            this.tomSelect.addItem(id, true)
        }
        this.id_filter = Number(id)
    },*/
    tomSettings(filtrosDisponibles?: IFilterDefinition[]) {
        const filtrosDisponiblesTomSettings = {
            ...getFiltrosDisponiblesOptions(
                this.id_filter || this.$store.active_filter.id,
                this.$store.user.default_filter,
                false,
                filtrosDisponibles.map(f => {
                    if (f.user_id == this.$store.user.id) {
                        f.opt_group = ' Creados por mí'
                    }
                    return f;
                })),
            onChange: (value, item) => {
                console.info('availableFiltersControl onChange', value, item)
                this.id_filter = value
                this.$store.active_filter.setActive(value)
            },
            item: function (data) {

                return `<span class="bg-gray-100 border">${data.name}</span>`;
            }

        };

        return filtrosDisponiblesTomSettings;
    },
    createAvailableFiltersControl(attempt = 0, cb: Function) {
        let filtrosDisponibles = this.$store.active_filter.filtrosDisponibles;
        if (!filtrosDisponibles.length && attempt < 3) {
            return waitFor(500).then(() => { this.debouncedCreateTomSelect(attempt + 1, cb) })
        }
        const tomSettings = this.tomSettings(filtrosDisponibles)
        console.info('createAvailableFiltersControl', tomSettings)
        if (this.tomSelect) {
            this.tomSelect.destroy()
            return setTimeout(() => {
                if (this.tomSelect) this.tomSelect.destroy()
                this.tomSelect = new TomSelect(
                    this.$el,
                    tomSettings
                );
                if (cb) cb()
            }, 200)
        }
        this.tomSelect = new TomSelect(
            this.$el,
            tomSettings
        );
        if (cb) cb()
        this.$store.active_filter.on('filters_loaded', () => {
            this.recreateAvailableFiltersControl()
        })
    },
    recreateAvailableFiltersControl() {
        if (this.tomSelect) {
            this.tomSelect.destroy()
        }
        this.debouncedCreateTomSelect(0, () => {
            setTimeout(() => {
                this.syncTomSelect()
            }, 500)
        })
    },
    get active_filter_store_id() {
        return this.$store.active_filter.id
    },
    init() {
        console.info('waiting for active_filter to be ready')
        this.$store.active_filter.once('filters_loaded', () => {

            this.createAvailableFiltersControl()

            this.$watch('active_filter_store_id', (id) => {
                this.syncTomSelect()
            })
            setTimeout(() => this.recreateAvailableFiltersControl(), 1000)
        });
        this.$store.active_filter.controls.set('availableFiltersControl', this)
        globalThis.availableFiltersControlInstance = this
        this.debouncedCreateTomSelect = Alpine.debounce(this.createAvailableFiltersControl.bind(this), 500)
    },
    syncTomSelect() {
        if (this.tomSelect) this.tomSelect.addItem(this.active_filter_store_id)
    }




} as AlpineDataComponent<IAvailableFiltersControl>)

export default availableFiltersControl