
import { IEnrichedFiltro, IFiltro } from "@lacasadejuana/types";
import { EnrichedFilter } from '../EnrichedFilter';



export type TEnriquecidoResult = {
    renderContacto: boolean;
    renderSelect: boolean;
    renderSingleDate: boolean;
    renderDateRange: boolean;
    renderNumber: boolean;
    renderText?: boolean;
};
export function enrichFilters(filtros: IFiltro[]): IEnrichedFiltro[] {
    return filtros
        .map((filtro, index) => enrichFilter(filtro))

}
export function enrichFilter(filtro: IFiltro, index?: number): IEnrichedFiltro {
    return filtro instanceof EnrichedFilter ? filtro : new EnrichedFilter(filtro);

}


