
import { IFiltro, TOption, VSearchType } from "@lacasadejuana/types";
import TomSelect from 'tom-select';

import { AlpineDataComponent } from '@/components';
import { RecursivePartial, TomSettings } from 'tom-select/dist/types/types';
import getFiltroAndCampo, { interfaceGetFiltroAndCampo } from '../decorators/getFiltroAndCampo';

export interface IRenderSelectControl extends interfaceGetFiltroAndCampo {
    get options(): TOption[],

    local_editing: false,
    slug_name: string;
    tomselect: TomSelect,
    stringifiedOptions(options: TOption[]): string,
    initTomSelect($el: HTMLElement): void,

}

export const filterRenderSelectControl = (filtro: IFiltro) => ({
    tables: false,
    ...getFiltroAndCampo(filtro),
    get options(): TOption[] {
        //@ts-ignore
        return this.$store.campos_busqueda.computeOptions(this.filtro.campo_busqueda).slice(0).sort((a, b) => a.id - b.id) as TOption[]
    },
    tomselect: null,
    get valor_busqueda() {
        return filtro.valor_busqueda
    },
    init() {
        this.initTomSelect(this.$el)
        this.$watch('valor_busqueda', (newOptions, oldOptions) => {
            console.log({ changed: 'valor_busqueda', oldOptions, newOptions })
            if (this.tomselect) this.tomselect.addItems(newOptions)
        })
        this.$watch('options', (newOptions, oldOptions) => {
            //@ts-ignore
            let newOptions_string = this.stringifiedOptions(newOptions)
            //@ts-ignore
            let oldOptions_string = this.stringifiedOptions(oldOptions)
            if (newOptions_string === oldOptions_string) return;
            //@ts-ignore
            this.tomselect.clear();
            this.tomselect.clearOptions();

            this.tomselect.addOptions(newOptions.sort((a, b) => a.id - b.id))
        })
        this.filtro.selectControl = this
        this.filtro.valor_busquedaFn = (negocio) => {
            let value = negocio[this.filtro.slug_name] ?? negocio._extra_props[this.filtro.slug_name],
                valor_busqueda = Alpine.raw(this.valor_busqueda),
                operation = this.filtro.tipo_busqueda as VSearchType

            return (operation === VSearchType.IN) ? valor_busqueda.includes(value) : !valor_busqueda.includes(value)
        }
    },
    stringifiedOptions(options) {
        //@ts-ignore
        return Object.values(options || this.options).slice(0).map(e => e.nombre_completo || e.value).sort((a, b) => {
            return String(a).localeCompare(String(b))
        }).join(',')
    },

    initTomSelect($el) {
        if (this.tomselect) this.tomselect.destroy();
        let plugins = ['remove_button']
        if (this.options.length > 10) plugins = plugins.concat(['dropdown_input', 'checkbox_options'])

        let tsettings = {

            create: false,
            hidePlaceholder: true,
            items: (Array.isArray(filtro.valor_busqueda) ? filtro.valor_busqueda : [filtro.valor_busqueda]).map(String),
            labelField: 'name',
            maxItems: null,
            options: this.options.sort((a, b) => a.id - b.id),
            sortField: [{ field: '$order' }],
            lockOptgroupOrder: true,
            placeholder: `Valor búsqueda`,
            plugins,
            valueField: 'value',
            searchField: ['name', 'label', 'value'],
            onChange: (value, item, event) => {
                console.log('onChange', value, item, event)
                filtro.valor_busqueda = value
            },


        } as RecursivePartial<TomSettings>
        if (['codigo_interno', 'id'].includes(this.campo.slug_name)) {
            tsettings = {
                ...tsettings,
                render: {
                    option: function (data) {
                        let [value, name] = data.name.split('|')
                        return `<div class='flex align-items-center'  role='option'>
                            <span style="font-family:monospace;font-size:0.95em;padding-top:0.05em" class='flex-shrink-1 mr-2'>[${value}]</span>
                             <span class='flex-grow-1'>${name}</span>
                        </div>`;
                    },
                    item: function (data) {
                        let [value, name] = data.name.split('|')
                        return `<span class="bg-gray-100 border">[${value}] ${name}</span>`;
                    }
                }
            }
        }

        this.tomselect = new TomSelect($el, tsettings)

    }

} as AlpineDataComponent<IRenderSelectControl>);
